import React from 'react'
import { useEffect, useState } from 'react';
import validation from '../../validation';

const init = {
    subscriber_name: '',
    subscriber_email: '',
    subscriber_phone: '',
    subscriber_campany_name: '',
    subscriber_message: ''
};

const validateEmail = (email) =>
    String(email)
        .toLowerCase()
        .match(
            /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        );
export default function TaleSales() {
    const [state, setState] = useState(init);
    const [store, setStore] = useState([]);
    const [errors, setErrors] = useState({});
    // const { enqueueSnackbar, closeSnackbar } = useSnackbar();

    // console.log(errors.length);
    const handleChange = (e) => {
        const { name, value } = e.target;
        setState({ ...state, [name]: value });
    };

    const handleClick = (e) => {
        e.preventDefault()
        setErrors(validation(state));
        if (
            state.subscriber_name !== '' &&
            state.subscriber_email !== '' &&
            validateEmail(state.subscriber_email) &&
            state.subscriber_phone !== '' &&
            state.subscriber_phone.length === 10 &&
            state.subscriber_campany_name !== '' &&
            state.subscriber_message !== '' &&
            state.subscriber_message.length >= 15
        ) {
            const payload = {
                subscriber_name: state.subscriber_name,
                subscriber_email: state.subscriber_email,
                subscriber_phone: state.subscriber_phone,
                subscriber_campany_name: state.subscriber_campany_name,
                subscriber_message: state.subscriber_message,
                subscriber_type: 'TeleSales'
            };

            fetch(`https://admin-api-dev.task.task-mo.com/api/create-subscriber`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(payload)
            }).then((res) => {
                res.json();
            });

            // setTodo([...Todo, payload]);
            // enqueueSnackbar('Form Successfully Submited', {
            //     variant: 'success',
            //     action: (key) => (
            //         <MIconButton size="small" onClick={() => closeSnackbar(key)}>
            //             <Icon icon={closeFill} />
            //         </MIconButton>
            //     )
            // });

            setStore([...store, payload]);
            console.log("store", store);

            setState(init);
        }
    };

    const navRef1 = React.useRef(null);
    const navRef2 = React.useRef(null);
    const navRef3 = React.useRef(null);

    const onToggleClick1 = (e) => {
        navRef1.current.classList.toggle("show");
    }

    const onToggleClick2 = (e) => {
        navRef2.current.classList.toggle("show");
    };

    const onToggleClick3 = (e) => {
        navRef3.current.classList.toggle("show");
    };

    useEffect(() => {
        window.scrollTo(0, 0)
    }, [])
    return (
        <div style={{ marginTop: "140px" }}>
            {/* <!--====== Feature Section Start ======--> */}
            <section class="feature-section section-gap-bottom">
                <div class="container">
                    <div class="row align-items-center justify-content-center">
                        <div class="col-lg-6 col-md-10">
                            <div class="feature-img text-lg-left text-center content-mb-md-50">
                                <img src="assets/img/illustrations/telesales_image.svg" alt="Image" />
                            </div>
                        </div>
                        <div class="font-montserrat col-lg-6 col-md-8 wow fadeInUp">
                            <div class="consultation-form-area pl-xl-3 content-mt-md-50">
                                <div class="consultation-form">
                                    <h3 class="title font-montserrat fw-600">Tele Sales</h3>
                                    <p class="subtitle">Share your details and requirements</p>
                                    <form action="#">
                                        <div class="font-montserrat input-field">
                                            <input class="font-montserrat" type="text" placeholder="Full Name" name="subscriber_name" value={state.subscriber_name} onChange={handleChange} />
                                            <p style={{ color: 'red' }}>{errors.subscriber_name}</p>
                                        </div>
                                        <div class="input-field">
                                            <input class="font-montserrat" type="email" placeholder="Email Address" name="subscriber_email" value={state.subscriber_email} onChange={handleChange} />
                                            {errors.subscriber_email && <p style={{ color: 'red' }}>{errors.subscriber_email}</p>}
                                        </div>
                                        <div class="input-field">
                                            <input class="font-montserrat" type="text" placeholder="Phone Number" name="subscriber_phone" value={state.subscriber_phone} onChange={handleChange} />
                                            {errors.subscriber_phone && <p style={{ color: 'red' }}>{errors.subscriber_phone}</p>}
                                        </div>
                                        <div class="input-field">
                                            <input class="font-montserrat" type="text" placeholder="Company Name" name="subscriber_campany_name" value={state.subscriber_campany_name} onChange={handleChange} />
                                            {errors.subscriber_campany_name && <p style={{ color: 'red' }}>{errors.subscriber_campany_name}</p>}
                                        </div>

                                        <div class="input-field">
                                            <textarea class="font-montserrat textarea" placeholder="Tell us what are you business requirements" name="subscriber_message" value={state.subscriber_message} onChange={handleChange}>
                                            </textarea>
                                            {errors.subscriber_message && <p style={{ color: 'red' }}>{errors.subscriber_message}</p>}
                                        </div>

                                        <div class="input-field">
                                            <button style={{ fontWeight: "600" }} onClick={handleClick} type="submit" class=" template-btn">Submit <i class="flaticon-right-arrow"></i></button>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            {/* <!--====== Feature Section End ======--> */}







            <div class="fancy-feature-sixteen mt-50 md-mt-100" id="feature">
                <div class="container">
                    <div class="block-style-eighteen">
                        <div class="row align-items-center">
                            <div class="col-lg-5" data-aos="fade-up" data-aos-duration="1200">
                                <div class="font-montserrat text-wrapper">
                                    <h3 class="fs-50 fw-600 font-montserrat">Tele Sales</h3>
                                    <p style={{ fontSize: "15px" }}>
                                        We understand that Tele Sales is an integral part of B2B Telemarketing. If you are on the lookout for the best telesales executives and telecallers and want to gain new clients for your business, Taskmo is here to help you. At Taskmo we have with us highly experienced professionals who are trained to influence customer decisions and contribute towards the success of your telemarketing campaign. <br/><br/>Tele sales not only helps to generate leads but also help to increase brand awareness by bringing new clients. For over half a decade, Taskmo has provided best telemarketing services and worked with businesses in industries like healthcare, pharmaceuticals, construction, consumer goods, education, travel and transportation and many more. Taskmo has been a  leading player in the telemarketing services sector and we are looking forward to providing exceptional and personalised telemarketing services that will help you stand out from the competition.
                                    </p>

                                </div>
                            </div>
                            <div class="col-lg-7" data-aos="fade-up" data-aos-duration="1200">
                                <div class="d-flex align-items-center justify-content-center">

                                    <img src="assets/img/illustrations/telesale_img1.svg" alt="Image" class="animate-float-bob-y" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>




            <div class="faq-classic pt-140 md-pt-120">
                <div class="container">
                    <div class="row">
                        <div class="col-lg-6">
                            <div class="title-style-one">
                                <h6 class="font-montserrat">Frequently Asked Questions</h6>
                                <h2 class="fs-50 fw-600 font-montserrat">Have any thought? Look here.</h2>
                            </div>
                        </div>
                        <div class="col-lg-6">
                            <div id="accordion" class="md-mt-60">
                                <div class="card">
                                    <div class="card-header" id="headingOne">
                                        <h5 class="mb-0">
                                            <button onClick={onToggleClick1} class="btn btn-link font-montserrat">
                                                What languages do you support?
                                            </button>
                                        </h5>
                                    </div>
                                    <div id="collapseOne" ref={navRef1} class="collapse">
                                        <div class="card-body font-montserrat">
                                            <p>
                                                Most of Taskmo’s clients require support primarily in English. However, our staff are fluent in regional languages and we offer multilingual support as well.
                                            </p>
                                        </div>
                                    </div>
                                </div>

                                <div class="card">
                                    <div class="card-header" id="headingThree">
                                        <h5 class="mb-0">
                                            <button onClick={onToggleClick2} class="btn btn-link font-montserrat">
                                                What types of clients do you work with?
                                            </button>
                                        </h5>
                                    </div>
                                    <div id="collapseThree" ref={navRef2} class="collapse">
                                        <div class="card-body font-montserrat">
                                            <p>
                                                Taskmo works with all types of clients. We work with companies ranging from the elite Fortune 500 companies, to startups and even personal brands.
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

